.banner {
   @include transition(background-position-x);
   position: relative;

   height: 30vh;
   min-height: 300px;
   margin: 0;

   background-repeat: no-repeat;
   background-attachment: scroll;
   background-position-x: 0;
   background-position-y: 50%;
   background-size: cover;
   box-shadow: inset  0  8px 8px -8px rgba($colorBlack, .5),
   inset  0 -8px 8px -8px rgba($colorBlack, .5);

   @include media(lg) {
      background-attachment: fixed;
      background-position-x: 50%;
      background-size: auto calc(100vmax + 150px);
   }
}
