.icon-blogger,
.icon-blogger-circled {
   &:before {
      @extend %transition;

      padding: $spacingXSm;

      color: $blogger !important;
   }
   &:hover {
      &:before {
         color: darken($blogger, 15%) !important;
      }
   }
}

