.navMain {
   .icon-github,
   .icon-twitter,
   .icon-linkedin,
   .icon-instagram {
      &:before {
         color: inherit !important;
      }
   }
   .icon-github:hover {
      &:before {
         color: $github-nav !important;
      }
   }
   .icon-twitter:hover {
      &:before {
         color: $twitter !important;
      }
   }
   .icon-linkedin:hover {
      &:before {
         color: $linkedin !important;
      }
   }
   .icon-instagram:hover {
      &:before {
         color: $instagram !important;
      }
   }
   .js-header-fixed & {
      .icon-github,
      .icon-twitter,
      .icon-linkedin, {
         &:before {
            color: $colorWhite !important;
         }
      }
      .icon-github:hover {
         &:before {
            color: $github-nav !important;
         }
      }
      .icon-twitter:hover {
         &:before {
            color: $twitter !important;
         }
      }
      .icon-linkedin:hover {
         &:before {
            color: $linkedin !important;
         }
      }
      .icon-instagram:hover {
         &:before {
            color: $instagram !important;
         }
      }
   }
