.projectGrid {
    @extend %flex;
    flex-flow: column nowrap;

    width: 100%;

    @include media(med) {
        flex-flow: row wrap;
    }

    .projectGrid-container {
        @extend %flex;
        position: relative;
        z-index: 1;

        align-items: center;
        justify-content: space-between;

        width: 100%;
        margin-bottom: 25px;
        box-shadow: 2px 2px 8px rgba($colorBlack, .25);

        @include media(med) {
            width: calc(50% - 12.5px);
            margin-right: 25px;

            &:nth-child(2n) {
                margin-right: 0;
            }
        }

        &:before {
            @extend %transition;
            position: absolute;
            z-index: 1;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            display: block;

            content: '';
            pointer-events: none;
        }
        &:nth-child(1n):before {
            background: rgba($colorBrand1, .75);
        }
        &:nth-child(2n):before {
            background: rgba($colorBrand2, .75);
        }
        &:nth-child(3n):before {
            background: rgba($colorBrand3, .75);
        }
        &:nth-child(4n):before {
            background: rgba($colorBrand4, .75);
        }
        &:after {
            @extend %transition;
            @extend %iconStyle;
            position: absolute;
            z-index: 4;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            display: block;
            pointer-events: none;

            width: 80px;
            height: 80px;
            margin: auto;

            content: '\e80b';
            font-size: 50px;

            opacity: 0;
            color: white;
        }
        &:hover {
            z-index: 2;

            cursor: pointer;

            &:before {
                transform: rotate(2.25deg) scale(1,1);
            }
            &:after {
                opacity: .85;
            }
            .projectGrid-overlay {
                opacity: .5;
            }
            .projectGrid-image {
                transform: scale(1.1, 1.1);
            }
        }
        .projectGrid-overlay {
            @extend %transition;
            position: absolute;
            z-index: 3;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            opacity: .05;
            background: $colorBlack;
        }
        .projectGrid-imageOverflow {
            @extend %flex;
            position: relative;
            z-index: 2;

            overflow: hidden;

            width: 100%;
            height: 300px;

            .projectGrid-title {
                @extend %transition;
                position: absolute;
                z-index: 2;
                top: 0;
                right: 0;
                left: 0;

                width: 100%;
                padding: 10px 20px;

                text-align: left;
                font-weight: bold;

                opacity: 1;
                color: $colorWhite;
                background: rgba($colorBlack, .75);
            }

            .projectGrid-image {
                @extend %transition;
                @extend %flex;
                z-index: 1;

                width: 100%;
                height: 300px;

                background-repeat: no-repeat;
                background-position: 50% 50%;
                background-size: cover;
            }
        }
    }
}
