ul.tabs {
    position: relative;

    width: 100%;
    margin: 0;
    padding: 0;
    .underline {
        @include transition;
        position: absolute;
        z-index: 5;
        bottom: 100%;
        left: 0;

        display: none;

        width: 0;
        height: 4px;

        transform-origin: left;

        background: $colorBrand3;
        @include media(lg) {
            display: block;
        }
    }

    li.tab {
        display: block;

        list-style: none;
        @include media(lg) {
            display: inline;
        }
        > a {
            @include transition;
            position: relative;
            z-index: 2;

            display: flex;

            align-items: center;

            padding: $spacingSm;

            font-weight: normal;

            color: colorBrand5;
            border-bottom-width: 3px;
            border-bottom-style: solid;
            border-bottom-color: $colorGreyLighter;
            @include media(lg) {
                display: inline-block;

                font-weight: bold;

                color: lighten($colorBrand5, 30%);
                border-bottom: none;
                &:after {
                    display: none;
                }
            }
            &:after {
                @extend %iconStyle;
                margin-left: auto;

                content: '\e80b';
            }
        }
        > div {
            z-index: 1;

            display: none;
            float: left;

            width: 100%;
            padding: $spacingLg;

            transform-origin: top;

            @include media(lg) {
                display: inline;

                margin-top: -100%;
                padding: $spacingLg $spacingBase 0;

                transform: scale(1, 0);

                border-top: 1px solid $colorGreyLighter;
            }
            > * {
                @include transition(opacity, .35s);
                transition-delay: .1s;
                opacity: 0;
            }
        }
        &.is-active {
            > a {
                font-weight: bold;

                color: $colorBrand5;
                border-bottom-color: $colorBrand3;
                &:after {
                    content: '\e80d';
                }
            }
            > div {
                display: inline;
                @include media(lg) {
                    margin-top: -1px;
                    margin-bottom: $spacingLg;

                    transform: scale(1, 1);
                }
                > * {
                    opacity: 1;
                }
            }
        }
    }
}
