.mobileDrawer {
   @include transition(right);
   position: fixed;
   z-index: 1000;
   top: 0;
   right: -250px;

   width: 250px;
   height: 100vh;

   background: $colorGreyLightest;

   .js-drawer-open & {
      right: 0;
   }
   nav {
      padding: $spacingBase $spacingSm;

      > ul {
         @extend %unstyledList;
         @extend %flex;
         flex-direction: column;

         > li {
            @extend %flex;
            margin: 0 0 10px;

            &:hover {
               &:nth-child(1) {
                  > a {
                     background: $colorBrand1;
                  }
               }
               &:nth-child(2) {
                  > a {
                     background: $colorBrand2;
                  }
               }
               &:nth-child(3) {
                  > a {
                     background: $colorBrand3;
                  }
               }
               &:nth-child(4) {
                  > a {
                     background: $colorBrand4;
                  }
               }
            }
            > a {
               @extend %transition;
               display: block;

               padding: $spacingSm;
               padding-left: $spacingBase;

               background: $colorGreyLighter;

               &:hover {
                  color: $colorWhite;
               }
            }
            &.has-children {
               > ul {
                  @extend %unstyledList;

                  //@extend %flex;
                  display: none;

                  flex-direction: row;
                  flex-shrink: 0;
                  flex-basis: 100%;
                  justify-content: center;

                  margin-top: 10px;

                  > li {
                     @extend %flex;
                     display: block;

                     flex-grow: 0;
                     align-items: flex-end;

                     margin-right: 10px;

                     &:last-child {
                        margin-right: 0;
                     }
                     > a {
                        padding: $spacingSm;

                        background: $colorGreyLighter;
                     }
                  }
               }
            }
         }
      }
   }
}

.headerMain-mobileToggle {
   @include fontSize(21px);
   display: block;

   color: $colorWhite;

   @include media(med) {
      display: none;
   }

   .js-header-fixed & {
      color: $colorWhite;
   }
}
