.sectionHead {
   @include transition(background-position-x);
   position: relative;

   display: flex;

   align-items: flex-end;

   min-height: 80vh;
   padding: 110px 0 0;

   background-image: url('../../image-library/aspens.jpg');
   background-repeat: no-repeat;
   background-attachment: scroll;
   background-position-x: 0;
   background-position-y: 50%;
   background-size: cover;
   box-shadow: inset  0 -8px 8px -8px rgba($colorBlack, .5);

   @include media(med) {
      align-items: center;
      justify-content: center;

      padding: 110px 0 ;
   }

   @include media(lg) {
      background-attachment: fixed;
      background-position-x: 50%;
      background-size: auto calc(100vmax + 150px);
   }
   .sectionhead-content {
      display: flex;

      flex-direction: column;
      align-items: center;
      justify-content: center;

      width: 100%;
      padding: 10px 20px;

      background: rgba($colorGreyDarkest, .8);
      @include media(med) {
         width: auto;
      }
      h1,
      h2 {
         position: relative;

         padding: 0;

         color: $colorWhite;
         text-shadow: 1px 1px 3px rgba($colorBlack, .4);
      }
      h2 {
         margin: 0;

         font-weight: 300;
      }
      h1 {
         @include fontSize(30px);
         display: inline-flex;

         align-items: center;
         justify-content: center;
         order: 2;

         margin: 5px 0;

         white-space: nowrap;
         @include media(med) {
            @include fontSize(40px);
            margin: 10px 0;
         }
         &:before,
         &:after {
            position: relative;
            top: 3px;

            flex-shrink: 1;

            width: 50px;
            height: 3px;

            content: '';

            background: $colorWhite;
            box-shadow: 1px 1px 3px rgba($colorBlack, .2);
            @include media(med) {
               width: 70px;
               height: 5px;
            }
         }
         &:before {
            margin-right: 10px;
         }
         &:after {
            margin-left: 10px;
         }
      }
      h2:first-of-type {
         @include fontSize(18px);
         order: 1;
         @include media(med) {
            @include fontSize(25px);
         }
      }
      h2:last-of-type {
         @include fontSize(14px);
         order: 3;
         @include media(med) {
            @include fontSize(20px);
         }
      }
   }
}
