ul,
ol {
   margin: $spacingBase 0;
   padding: 0;
   padding-left: $spacingXLg;

   text-align: left;

   li {
      margin: 0;
      margin-top: $spacingXSm;

      text-align: left;
      letter-spacing: .75px;

      ul,
      ol {
         margin: 0;
         padding-left: $spacingBase;

         li {
            margin-top: 0;
         }
      }
   }
}

.infoList {
   display: flex;

   flex-flow: column;

   margin: 0 $spacingSm;
   padding: 0 $spacingSm;

   list-style: none;

   text-align: center;
   text-indent: 0;

   border-left: 3px solid $colorGreyLighter;
   @include media(sm) {
      flex-flow: row wrap;
      justify-content: center;

      margin: 0;
      padding: 0;
      border-left: none;
   }
   li {
      @include fontSize(13px);
      margin: 0;
      padding: 0;

      list-style: none;

      text-indent: 0;
      font-weight: bold;
      @include media(sm) {
         &:after {
            margin-right: 10px;
            padding-left: 10px;

            content: '\2022';
         }
         &:last-child:after {
            display: none;
         }
      }
      span {
         font-weight: normal;
      }
   }
}
