.iconBlock {
   @extend %flex;
   flex-direction: column;
   justify-content: center;
   @include media(med) {
      flex-direction: row;
   }
   .iconBlock-group,
   .iconBlock-group a {
      @extend %flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      &:nth-child(2n) {
         margin: 0 40px;
      }
      i {
         @include fontSize(100px);
      }
      span {
         text-align: center;
         font-style: normal;
         &:first-of-type {
            @include fontSize(20px);
            font-weight: bold;
         }
         &:last-of-type {
            @include fontSize(14px);
            font-weight: normal;
         }
      }
   }
}

.iconBlock--vertCenter {
   position: absolute;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   margin: auto;
}

.iconBlock--links {
   .iconBlock-group {
      position: relative;

      padding-top: 120px;
      i {
         position: absolute;
         top: 0;
         right: 0;
         left: 0;

         width: 105px;
         margin: 0 auto;
         &:before {
            margin: 0;
            padding: 0;
         }
         &:last-of-type {
            transform: scale(1, 1);
            pointer-events: none;

            opacity: .5;
         }
      }
      &:hover {
         i:last-of-type {
            //top: -100px;
            top: -20px;

            transition: all .35s ease-in-out;
            transform: scale(2.75, 2.75);

            opacity: 0;
         }
      }
   }
}
