/***********************************
    Emulate Headings Styles
***********************************/
.h-h1 {
    @extend h1;
}
.h-h2 {
    @extend h2;
}
.h-h3 {
    @extend h3;
}
.h-h4 {
    @extend h4;
}
.h-h5 {
    @extend h5;
}
.h-h6 {
    @extend h6;
}


/***********************************
    Override Text Alignment
***********************************/
.h-alignLeft {
    text-align: left !important;
}
.h-alignCenter {
    text-align: center !important;
}
.h-alignRight {
    text-align: right !important;
}
.h-justify {
    text-align: justify !important;
}


/***********************************
    Internal Spacing
***********************************/
.h-space {
    padding: $spacingBase !important;
    &--xsm {
        padding: $spacingXSm !important;
    }
    &--sm {
        padding: $spacingSm !important;
    }
    &--lg {
        padding: $spacingLg !important;
    }
    &--xlg {
        padding: $spacingXLg !important;
    }
}
.h-spaceEnds {
    padding: $spacingBase 0 !important;
    &--xsm {
        padding: $spacingXSm 0 !important;
    }
    &--sm {
        padding: $spacingSm 0 !important;
    }
    &--lg {
        padding: $spacingLg 0 !important;
    }
    &--xlg {
        padding: $spacingXLg 0 !important;
    }
}
.h-spaceSides {
    padding: 0 $spacingBase !important;
    &--xsm {
        padding: 0 $spacingXSm !important;
    }
    &--sm {
        padding: 0 $spacingSm !important;
    }
    &--lg {
        padding: 0 $spacingLg !important;
    }
    &--xlg {
        padding: 0 $spacingXLg !important;
    }
}
.h-spaceBottom {
    padding: 0 0 $spacingBase !important;
    &--xsm {
        padding: 0 0 $spacingXSm !important;
    }
    &--sm {
        padding: 0 0 $spacingSm !important;
    }
    &--lg {
        padding: 0 0 $spacingLg !important;
    }
    &--xlg {
        padding: 0 0 $spacingXLg !important;
    }
}
.h-spaceTop {
    padding: $spacingBase 0 0 !important;
    &--xsm {
        padding: $spacingXSm 0 0 !important;
    }
    &--sm {
        padding: $spacingSm 0 0 !important;
    }
    &--lg {
        padding: $spacingLg 0 0 !important;
    }
    &--xlg {
        padding: $spacingXLg 0 0 !important;
    }
}
.h-spaceLeft {
    padding: 0 0 0 $spacingBase !important;
    &--xsm {
        padding: 0 0 0 $spacingXSm !important;
    }
    &--sm {
        padding: 0 0 0 $spacingSm !important;
    }
    &--lg {
        padding: 0 0 0 $spacingLg !important;
    }
    &--xlg {
        padding: 0 0 0 $spacingXLg !important;
    }
}
.h-spaceRight {
    padding: 0 $spacingBase 0 0 !important;
    &--xsm {
        padding: 0 $spacingXSm 0 0 !important;
    }
    &--sm {
        padding: 0 $spacingSm 0 0 !important;
    }
    &--lg {
        padding: 0 $spacingLg 0 0 !important;
    }
    &--xlg {
        padding: 0 $spacingXLg 0 0 !important;
    }
}


