/* Date Picker
========================================================================== */

.datepicker {
   position: relative;

   padding-bottom: 0;

   input[type='text'] {
      background-image: url('data:image/png;base64,');
      background-repeat: no-repeat;
      background-position: right 9px top 55%;
   }
   .datepicker-calendar {
      position: absolute;
      z-index: 1000;
      top: 105%;
      left: 0;

      overflow: hidden;

      box-sizing: content-box;
      width: 210px;

      border: 1px solid $colorGreyLight;
      border-radius: 5px;
      background: $colorGreyLight;
      box-shadow: 0 0 4px rgba(0, 0, 0, .25);

      .datepicker-calendar-headRow {
         position: relative;

         display: flex;
         overflow: hidden;

         flex-direction: row;
         justify-content: center;

         width: 100%;
         height: 30px;

         background: $colorGreyDarker;

         div {
            color: $colorWhite;

            &:last-child,
            &:first-child {
               position: relative;

               display: flex;

               flex: 0 0 30px;
               align-items: center;
               justify-content: center;

               width: 30px;
               height: 30px;

               cursor: pointer;

               &:after {
                  display: block;

                  width: 10px;
                  height: 10px;

                  content: '';
                  line-height: 30px;

                  border-top: 2px solid $colorWhite;
                  border-right: 2px solid $colorWhite;
               }
               &:hover {
                  background: $colorGrey;
               }
            }
            &:first-child {
               border-right: 1px solid $colorGrey;

               &:after {
                  margin-left: 4px;

                  transform: rotate(-135deg);
               }
            }
            &:last-child {
               border-left: 1px solid $colorGrey;

               &:after {
                  margin-right: 4px;

                  transform: rotate(45deg);
               }
            }
            &:nth-child(2) {
               position: relative;

               flex: 1 1 100%;

               height: 30px;
               margin: 0 auto;

               text-align: center;
               font-size: 15px;
               font-weight: bold;
               line-height: 30px;

               &::selection {
                  background-color: transparent;
               }
            }
         }
      }
      .datepicker-calendar-dayRow {
         position: relative;

         display: flex;
         overflow: hidden;

         flex-direction: row;

         width: 100%;
         height: 30px;

         background: lighten($colorGreyLightest, 4%);

         div {
            position: relative;

            width: 30px;
            height: 30px;

            text-align: center;
            font-size: 14px;
            font-weight: bold;
            line-height: 30px;

            color: $colorGreyDarker;
            border-bottom: 1px solid $colorGreyLight;

            &::selection {
               color: currentColor;
               background-color: transparent;
            }
            &:last-child {
               border-right: none;
            }
         }
      }
      .datepicker-calendar-bodyRow {
         position: relative;

         display: flex;
         overflow: hidden;

         flex-flow: row wrap;

         width: 100%;

         div {
            position: relative;

            min-width: 30px;
            height: 30px;

            text-align: center;
            font-size: 13px;
            font-weight: normal;
            line-height: 30px;

            color: $colorGreyDarker;
            border: 1px solid $colorGreyLight;
            border-top: none;
            border-left: none;
            background: lighten($colorGreyLightest, 4%);

            &::selection {
               color: currentColor;
               background-color: transparent;
            }
            &.empty {
               cursor: default;

               border-right-color: transparent;
               background: transparent;

               &.last {
                  border-right: 1px solid $colorGreyLight;
               }
               &:hover {
                  cursor: default;

                  background: transparent;
               }
            }
            &:nth-child(7n) {
               border-right: none;
            }
            &.today {
               font-weight: bold;

               background: $colorGreyLighter;
            }
            &.selected {
               font-weight: bold;

               color: $colorWhite;
               background: $colorSuccess;

               &::selection {
                  color: currentColor;
               }
            }
            &:hover {
               cursor: pointer;

               color: $colorWhite;
               background: $colorGreyDarker;

               &::selection {
                  color: currentColor;
               }
            }
         }
      }
   }
}
