/* Print */

@media print {
   @page {
      overflow: visible !important;

      margin: .25cm !important;
      size: auto !important;
   }

   body {
      display: block !important;
      overflow: visible !important;
      overflow-x: visible !important;
      overflow-y: visible !important;

      width: initial !important;
      max-width: initial !important;
      height: initial !important;
      max-height: initial !important;
      margin: 0 !important;
   }

   * {
      position: relative !important;

      float: none !important;
      clear: both !important;
      overflow: visible !important;
      overflow-x: visible !important;
      overflow-y: visible !important;

      color: $colorGreyDarker !important;
      background: transparent !important;
      box-shadow: none !important;
      text-shadow: none !important;
      -ms-filter: none !important;
      filter: none !important;
   }

   //Hide everything that is not the resume
   .mobileDrawer,
   header,
   section:not(#resume),
   .banner,
   #resume h3,
   footer {
      display: none !important;
   }

   #resume,
   #resume * {
      display: block;
   }

   #resume {
      display: block !important;

      margin: 0 !important;
      padding: 0 !important;
   }

   .resume {
      display: block !important;

      padding: 0 !important;
   }

   .resume-header {
      p {
         margin: 0 !important;
         padding: 0 !important;
         display: block !important;
         text-align: center !important;
         &:first-child {
            margin-bottom: 7px !important;

            text-align: center !important;
            font-size: 28px !important;
            font-weight: bold !important;
            line-height: 28px !important;
         }
         &:last-child {
            font-size: 13px !important;
            margin: 0 auto !important;
            span {
               display: inline-block !important;
               margin: 0 !important;

               white-space: nowrap !important;
               font-size: 13px !important;
               font-weight: bold !important;
               &:first-child {
                  margin-left: 0 !important;
                  color: $colorBrand1 !important;
               }
               &:nth-child(2) {
                  color: $colorBrand2 !important;
               }
               &:nth-child(3) {
                  color: $colorBrand3 !important;
               }
               &:last-child {
                  margin-right: 0 !important;
                  color: $colorBrand4 !important;
                  &:after {
                     display: none;
                  }
               }
               &:after {
                  padding-right: 5px;
                  padding-left: 15px;
                  margin-left: -10px;
                  color: $colorBrand5 !important;

                  content: '\2022';
               }
            }
         }
      }
   }

   .resume-body {
      display: block !important;
      overflow: visible !important;
      .resume-section {
         display: block !important;

         padding-top: 0 !important;

         border-top: none !important;
         &:first-of-type {
            margin-top: 0 !important;
         }
         &:last-of-type {
            margin-top: 0 !important;
            padding-top: 0 !important;
         }
         .resume-section-head {
            left: 0 !important;

            display: block !important;

            width: 100%;
            margin-top: 0 !important;
            margin-bottom: $spacingSm !important;
            padding-left: $spacingSm !important;

            transform: rotate(0deg) !important;
            font-size: 18px!important;

            color: $colorGreyDarker !important;
            border-bottom: 2px solid $colorBrand1!important;
            background: transparent !important;
            box-shadow: none !important;
         }
         .resume-section-content {
            display: block !important;

            padding-right: 0 !important;
            padding-left: 0 !important;

            font-size: 12px!important;
            .row {
               display: block !important;
               .column.sm-12 {
                  display: inline-block !important;

                  width: 49%;
               }
            }
            ul {
               font-size: 12px!important;
               ul {
                  font-size: 12px!important;

                  border-top: 1px solid $colorGreyDark !important;
               }
               li {
                  margin-bottom: 0 !important;

                  font-size: 12px!important;
                  &:before {
                     color: $colorBrand3 !important;
                  }
                  &:hover:before {
                     color: $colorBrand3 !important;
                  }
               }
            }
            .resume-content-item {
               display: block !important;

               font-size: 12px !important;
               > div {
                  &:first-of-type {
                     border-left-color: $colorBrand1 !important;
                     strong,
                     span {
                        display: inline-block !important;
                     }
                  }
               }
               div {
                  font-size: 12px !important;
                  p {
                     margin-top: 0 !important;
                     margin-bottom: 8px !important;

                     font-size: 12px !important;
                     &:last-of-type {
                        margin-bottom: 0 !important;
                        padding-bottom: 0 !important;
                     }
                  }
                  .infoList {
                     display: block !important;
                     li {
                        display: inline-block !important;
                        a {
                           display: inline !important;
                        }
                        &:after {
                           display: inline !important;
                        }
                        &:last-of-type:after {
                           display: none !important;
                        }
                     }
                  }
               }
            }
         }
      }
   }
   .resume-printInfo {
      display: block !important;
      visibility: visible !important;

      width: auto !important;
      height: auto !important;

      opacity: 1 !important;
      &.pageBreak {
         page-break-before: always !important;
      }
   }
}
