.navMain {
   @include fontSize($fontSizeLg);
   position: relative;

   display: none;

   height: 100%;

   font-family: $fontHighlight;

   @include media(med) {
      display: flex;
   }

   > ul {
      @extend %flex;
      @extend %unstyledList;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      height: 100%;
      margin-top: 0;

      list-style: none;

      > li {
         position: relative;

         padding: 5px 10px;

         > a {
            position: relative;

            display: block;

            width: 100%;
            height: 100%;

            color: $colorWhite;

            > span {
               position: absolute;
               z-index: -1;
               top: 0;
               right: 0;
               bottom: 0;
               left: 0;

               width: 2px;
               height: 2px;
               margin: auto;

               transition: none;

               opacity: 1;
               border-radius: 50% !important;
               box-shadow: 0 0 0 0 rgba($colorWhite, .25);
            }
         }
         &:hover {
            cursor: pointer;

            > a {
               > span {
                  @include transition(all, .5s, ease-in-out);
                  opacity: 0;
                  box-shadow: 0 0 0 50px rgba($colorWhite, .25);
               }
            }
            &:first-child {
               > a {
                  color: $colorBrand1;
               }
            }
            &:nth-child(2) {
               > a {
                  color: $colorBrand2;
               }
            }
            &:nth-child(3) {
               > a {
                  color: $colorBrand3;
               }
            }
            &:nth-child(4) {
               > a {
                  color: $colorBrand4;
               }
            }
         }
         &.has-children {
            > a:after {
               @include transition(opacity);
               position: absolute;
               top: 40%;
               right: -15px;

               display: block;

               width: 0;
               height: 0;

               content: '';

               opacity: 0;
               border: 5px solid transparent;
               border-top-color: currentColor;
            }
            > ul {
               @extend %unstyledList;
               @extend %flex;
               @extend %transition;
               position: absolute;
               right: -15px;

               padding-top: 0;

               transform: scale(1.5, 1.5);
               pointer-events: none;

               opacity: 0;

               > li {
                  @extend %flex;
                  flex-direction: row;

                  > a {

                     > i {
                        @include fontSize(15px);
                        display: block;

                        margin-left: 10px;
                     color: $colorWhite;
                     }
                  }
               }
            }
            &:hover {
               > a:after {
                  opacity: 1;
               }
               > ul {
                  transform: scale(1, 1);
                  pointer-events: all;

                  opacity: 1;
               }
            }
         }
      }
   }
   .js-header-fixed & {
      margin-top: -5px;

      > ul > li > a {
         color: $colorWhite;
      }
   }
}
