.resume {
   padding-top: $spacingXSm;
   .resume-body {
      .resume-section {
         z-index: 1;

         display: flex;

         flex-direction: column;
         align-items: center;

         margin-top: $spacingSm;
         padding-top: $spacingSm;

         border-top: 1px dashed $colorGreyLight;
         &:first-of-type,
         &.resume-printInfo {
            border-top: none;
         }
         .resume-section-head {
            @include fontSize(24px);
            position: relative;
            z-index: 1;
            left: -21px;

            display: inline-block;

            align-self: flex-start;

            margin: $spacingBase 0;
            padding: $spacingSm $spacingBase;

            transform: rotate(-8deg);

            color: $colorWhite;
            box-shadow: 1px 2px 8px rgba($colorBlack, .4);
         }
         &:nth-of-type(1n) {
            .resume-section-head {
               background: $colorBrand2;
            }
         }
         &:nth-of-type(2n) {
            .resume-section-head {
               background: $colorBrand3;
            }
         }
         &:nth-of-type(3n) {
            .resume-section-head {
               background: $colorBrand4;
            }
         }
         &:nth-of-type(4n) {
            .resume-section-head {
               background: $colorBrand5;
            }
         }
         .resume-section-content {
            width: 100%;
            padding-left: $spacingSm;
            ul.list-title {
               display: block;

               margin: 0;
               padding: 0;

               list-style: none;

               text-indent: 0;
               li {
                  @include fontSize(13px);
                  display: block;

                  margin: 0;
                  padding: 0 15px;

                  font-weight: normal;
                  @include media(med) {
                     @include fontSize(14px);
                     margin: 0 0 10px;
                  }
               }
               > li {
                  @include fontSize(18px);
                  padding: 0 0;

                  font-weight: bold;
               }
               ul {
                  display: block;

                  margin: 0;
                  margin-top: 3px;
                  padding: 0;
                  padding-top: 10px;

                  list-style: none;

                  text-indent: 0;

                  border-top: 3px solid $colorBrand1;
                  li {
                     display: block;
                     &:before {
                        @extend %iconStyle;
                        @extend %transition;
                        margin-right: 5px;

                        content: '\e808';

                        color: inherit;
                     }
                     &:hover {
                        &:before {
                           color: $colorBrand3;
                        }
                     }
                  }
               }
            }
            .resume-content-item {
               display: block;
               > div {
                  display: block;
                  &:first-of-type {
                     margin-left: $spacingXSm - 2;
                     padding-left: $spacingXSm - 2;

                     border-left-width: 7px;
                     border-left-style: solid;
                     border-left-color: $colorBrand1;
                     strong {
                        display: inline-block;

                        margin-right: 5px;

                        text-transform: uppercase;
                     }
                     span {
                        display: inline-block;

                        text-transform: capitalize;
                        font-style: italic;
                     }
                  }
                  &:not(:first-of-type) {
                     margin-left: $spacingXSm;
                     padding-bottom: $spacingXSm;
                     padding-left: $spacingXSm;

                     border-left: 3px solid $colorGreyLighter;
                  }
                  p {
                     display: block;
                     margin: 0;
                     padding-bottom: 5px;
                     &:first-of-type {
                        @include fontSize(12px);
                        font-weight: bold;
                     }
                     &:last-of-type {
                        padding-bottom: $spacingSm;
                     }
                  }
               }
            }
         }
      }
   }
   .resume-printInfo {
      display: none;
      visibility: hidden;

      width: 0;
      height: 0;

      opacity: 0;
   }
}
