.headerMain {
   @extend %flex;
   @extend %transition;
   position: fixed;
   z-index: 1000;
   top: 0;
   left: 0;

   flex: 0 1 auto;
   justify-content: center;

   width: 100%;
   min-height: 115px;
   padding: $spacingSm 0;

   font-family: $fontHighlight;

   color: $colorGreyDark;
   background: transparent;

   .header-mainContainer {
      @extend %flex;
      @extend %contained;
      flex-flow: row wrap;
      align-items: stretch;
      justify-content: flex-start;

      margin: 0;
      padding: 0 $spacingBase;

      .headerMain-logo {
         @extend %flex;
         position: relative;

         align-items: center;

         max-width: 50%;

         img {
            @include transition(all, .35s);
            position: absolute;
            top: 0;
            left: 0;

            min-width: 110px;
            min-height: 110px;
         }
         img.logo-full {
            z-index: 1;

            transform: scale(2.5, 2.5);
            pointer-events: none;

            opacity: 0;
         }
         img.logo-empty {
            z-index: 2;

            transform: scale(1, 1);
            pointer-events: all;

            opacity: 1;
         }
      }
      .headerMain-nav {
         @extend %flex;
         align-items: center;
         justify-content: flex-end;

         margin-left: auto;
      }
   }
   // &.js-header-fixed--up {
   //    top: -100px;
   // }
   // &.js-header-fixed--down {
   //    top: 0;
   // }
   // &.js-header-dark.js-header-fixed--down,
   // .js-header-fixed--up {
   &.js-header-fixed {
      min-height: 80px;

      background-color: rgba($colorGreyDarkest, 1);

      .headerMain-logo {
         img {
            min-width: 75px;
            min-height: 75px;
            margin-left: 15px;
         }
         img.logo-full {
            transform: scale(1, 1);
            pointer-events: all;

            opacity: 1;
            top: -8px;
         }
         img.logo-empty {
            transform: scale(2.5, 2.5);
            pointer-events: none;

            opacity: 0;
         }
      }
   }
   .js-drawer-open & {
      left: -250px;
   }
}
