figure {
    position: relative;

    display: block;

    margin: 0;

    @include media(med) {
        max-width: 25%;
    }

    img {
        position: relative;
        display: block;

        margin-right: auto;
        margin-left: auto;
    }
    figcaption {
        @include fontSize(14px);

        position: static;

        caption-side: bottom;

        text-align: center;
        font-style: italic;

        color: $colorGreyDark;
    }
    &.captionTop {
        figcaption {
            caption-side: top;
        }
    }
}
