@mixin media($query, $print:false) {
      @if $query == xsm-only {
         @media all and (min-width:$startXSm) and (max-width:$endXSm) {
            @content;
         }
      }
      @else if $query == xsm {
         @media all and (min-width:$startXSm) {
            @content;
         }
      }
      @else if $query == sm-only {
         @media all and (min-width:$startSm) and (max-width:$endSm) {
            @content;
         }
      }
      @else if $query == sm {
         @media all and (min-width:$startSm) {
            @content;
         }
      }
      @else if $query == med-only {
         @media all and (min-width:$startMed) and (max-width:$endMed) {
            @content;
         }
      }
      @else if $query == med {
         @media all and (min-width:$startMed) {
            @content;
         }
      }
      @else if $query == lg-only {
         @media all and (min-width:$startLg) and (max-width:$endLg) {
            @content;
         }
      }
      @else if $query == lg {
         @media all and (min-width:$startLg) {
            @content;
         }
      }
      @else if $query == xlg-only {
         @media all and (min-width:$startXLg) and (max-width:$endXLg) {
            @content;
         }
      }
      @else if $query == xlg {
         @media all and (min-width: $startXLg) {
            @content;
         }
      }
      @else if $query == xxlg {
         @media all and (min-width: $startXXLg) {
            @content;
         }
      }
      @else {
         @media all and (min-width: $query) {
            @content;
         }
      }
   
}
